export function getContrastHex(hexcolor) {
  const color = hexcolor.replace('#', '');
  return parseInt(color, 16) > 0xffffff / 2 ? '--color-gray-100' : '--color-white';
}
export const convertString = {
  constant(str) {
    return str.toUpperCase().replace(/-/g, '_').replace(/\s/g, '');
  },

  cammel(str) {
    return str.replace(/ /g, '').split('-').map((word, index) => {
      if (index) {
        return "".concat(word[0].toUpperCase()).concat(word.slice(1));
      }

      return word;
    }).join('');
  }

};
/**
 * Check if contain child element inside of parent element
 * {HTMLElement} parent element
 * {HTMLElement} child element to be checked
 */

export const containsNode = (parent, child) => parent === child || parent.contains && parent.contains(child);
/**
 * Check if target is inside of list elements
 * {HTMLElement} target element to be checked
 * {HTMLElement[]} elements reference element where will it be checked the target
 */

export const targetWithin = (target, elements) => elements.some(node => node && containsNode(node, target));
export const addLeadingZeros = (number, maxValue) => String(number).padStart(maxValue, '0');
export const KINDS = {
  DEFAULT: 'default',
  PRIMARY: 'primary',
  SUCCESS: 'success',
  DANGER: 'danger',
  WARNING: 'warning',
  HELP: 'help'
};