const isNull = o => o === null
const throwError = (message, err = '') => {
  throw new Error(`front-hub: ${message}\n\n${err}`)
}
const isPromise = p => p instanceof Promise || (p && typeof p.then === 'function')
const isFunction = f => typeof f === 'function'
const isValid = o => !isNull(o)
const isObject = o => !isNull(o) && typeof o === 'object' && o.constructor === Object

function mutate(target) {
  return function() {
    merge.call(void 0, [target, ...Array.from(arguments)])
  }
}

function merge(...sources) {
  let acc = {}
  for (const source of sources) {
    if (source instanceof Array) {
      if (!(acc instanceof Array)) {
        acc = []
      }
      acc = [...acc, ...source]
    } else if (source instanceof Object) {
      for (const entry of Object.entries(source)) {
        const key = entry[0]
        let value = entry[1]
        if (value instanceof Object && key in acc) {
          value = merge(acc[key], value)
        }
        acc = { ...acc, [key]: value }
      }
    }
  }
  return acc
}

module.exports = {
  mutate,
  isPromise,
  isNull,
  throwError,
  isFunction,
  isValid,
  isObject,
  merge,
}
