import { useEffect } from 'react'
import { useHistory } from 'react-router-dom'
import { createBrowserHistory } from 'history'

import useURLManager from '../../url-manager'

const urlManager = useURLManager(createBrowserHistory())

export default function ({ microfrontend }) {
  const history = useHistory()
  useEffect(() => urlManager(history, microfrontend.name), [history, microfrontend])
  return null
}
