import React from 'react';
import PropTypes from 'prop-types';
import styled, { css } from 'styled-components';

const Style = css`
  height: 100%;
  display: flex;
  position: relative;
  align-items: center;

  ${({ secondary }) =>
    secondary &&
    css`
      &:not(:first-child) {
        margin-left: var(--size-spacing-02);
      }
    `}
`;

const NavItem = styled(({ secondary, children, ...rest }) => <li {...rest}>{children}</li>)`
  ${Style}
`;

NavItem.defaultProps = {
  children: '',
  secondary: false,
};

NavItem.propTypes = {
  children: PropTypes.oneOfType([PropTypes.element, PropTypes.string, PropTypes.array, PropTypes.node]),
  secondary: PropTypes.bool,
};

export default NavItem;
