module.exports = {
  'publicPath': () => './',
  'rbac': {
  'role': context => {
      const isActive = context.user.account.plan.status === 'active';
      const planName = context.user.account.plan.planName.toLocaleLowerCase();

      switch (planName) {
        case 'basic':
          return isActive ? 'basicActive' : 'basicTrial';
        case 'entrylevelbasic':
          return isActive ? 'basicActive' : 'basicTrial';
        case 'entrylevellight':
          return isActive ? 'lightActive' : 'lightTrial';
        case 'pro':
          return planName;
        case 'enterprise':
          return planName;
        default:
          return 'pro';
      }
    },
  'permissions': {
  'dashboard': ["dashboard"],
  'home': ["home"],
  'attract': ["attract","socialMediaPosts","facebookAds","keywordsDashboard","onPageSeo"],
  'convert': ["convert","landingPages","forms","popUps","customFields","whatsAppButton"],
  'engage': ["engage","leadBase","leadScoring","leadTracking","leadSegmentation","marketingAutomation","email"],
  'analyze': ["analyze","marketingBi","channelAnalysis","reach","mostAccessedPages","reports"],
  'warning': ["trialRemainingDays","upgrade"],
  'productMenu': ["rdStationCRM","rdStationAppStore","rdStationMarketplace","rdUniversity","referAndEarn","rdStationPartners"],
  'accountMenu': ["account","integrations","successPlans","myJourney","myAccount","exit"],
},
  'grants': {
  'lightTrial': ["home:*","attract:attract","convert:*","analyze:analyze","engage:*","warning:trialRemainingDays","productMenu:*","accountMenu:*","not convert:forms","not engage:leadScoring","not engage:leadTracking","not productMenu:referAndEarn","not productMenu:rdStationPartners","not accountMenu:successPlans","not accountMenu:myJourney"],
  'lightActive': ["lightTrial","warning:upgrade","not warning:trialRemainingDays"],
  'basicTrial': ["home:*","attract:*","convert:*","engage:*","analyze:*","warning:trialRemainingDays","not analyze:marketingBi","not engage:leadScoring","not engage:leadTracking"],
  'basicActive': ["basicTrial","warning:upgrade","not warning:trialRemainingDays"],
  'pro': ["dashboard:*","attract:*","convert:*","engage:*","analyze:*","productMenu:*","accountMenu:*"],
  'enterprise': ["pro"],
},
},
  'name': "navbar-marketing",
  'version': "1.0.39",
}