const withSimilarPath = path => {
  const windowLocationPath = window.location.pathname.replace('/', '').split('/');
  const arrPath = path.replace('/', '').split('/');
  return arrPath.length === windowLocationPath.length && arrPath[0] === windowLocationPath[0];
};

const normalizeCurrentPath = (path, index) => {
  const windowLocationPath = window.location.pathname.replace('/', '').split('/');
  if (path === ':param' || path === windowLocationPath[index]) {
    return windowLocationPath[index];
  }
  return path;
};

const isEqualWindowLocationPath = path => {
  const arrayPath = path.replace('/', '').split('/');
  const pathNormalized = arrayPath.map((pathItem, index) => normalizeCurrentPath(pathItem, index));
  return pathNormalized.join('/') === window.location.pathname.replace('/', '');
};

const includesSearch = arrayPaths => arrayPaths.includes(`${window.location.pathname}${window.location.search}`);

export const isPathActive = (arrayPaths, isMatchExact = false) => {
  if (isMatchExact) {
    return includesSearch(arrayPaths);
  }

  const filtered = arrayPaths.filter(withSimilarPath);
  if (!filtered.length) {
    return false;
  }
  return filtered.some(isEqualWindowLocationPath);
};

export default isPathActive;
