/* eslint-disable import/prefer-default-export */
import { application } from '../../../core/backbone'
import microFrontendProduction from './hub'

import { rbac, name, version } from 'front-hub-application-host-configuration-prod'

const microfrontend = application()

export const Connect = Component => {
  if (typeof microfrontend.connect !== 'function') throw new Error('front-hub: private connect function was not defined before public Connect call')
  return microfrontend.connect(configuration => {
    // we should remove this call to avoid coupling between front-hub and microfrontends builds
    configuration.rbac = rbac
    configuration.name = name
    configuration.version = version
    return microFrontendProduction(Component, configuration)
  })
}
