import { useState, useContext, useEffect } from 'react'
import { FronthubContext } from 'front-hub-context-api'
import grants from '../../authorization-api/grants'
import { isPromise } from '../../../../core/object'
import Authorizer from './authorizer'
import withContext from '../with-context'

export default function useAuthorization() {
  const { rbac: lazyRBAC } = useContext(FronthubContext)
  const [rbacAvailable, setRBACAvailable] = useState(lazyRBAC)

  let authorization
  let cancel
  if (isPromise(rbacAvailable)) {
    cancel = rbacAvailable.then(rbac => {
      setRBACAvailable({
        ...grants(rbac || {}),
        role: rbac.role,
      })
    }).cancel
  } else {
    authorization = rbacAvailable
  }
  useEffect(() => cancel, [])

  const Component = withContext(Authorizer(authorization))
  Component.Allowed = withContext(Authorizer(authorization))
  Component.displayName = 'Allowed'
  Component.Allowed.displayName = Component.displayName
  Component.Denied = withContext(Authorizer(authorization, false))
  Component.Denied.displayName = 'Denied'
  return Component
}
