import { useRef, useEffect } from 'react';
import { accessible } from './accessibility';
export const useEventListener = (eventName, handler, element = window) => {
  const savedHandler = useRef();
  useEffect(() => {
    savedHandler.current = handler;
  }, [handler]);
  useEffect(() => {
    const isSupported = element && element.addEventListener;
    if (!isSupported) return;

    const eventListener = event => savedHandler.current(event);

    element.addEventListener(eventName, eventListener);
    return () => {
      element.removeEventListener(eventName, eventListener);
    };
  }, [eventName, element]);
};
export const useAccessibleKeyboardNavigationHandler = (ref, escapeCb) => ({
  key
}) => {
  const {
    navigateToNextChild,
    navigateToPrevChild
  } = accessible(ref.current);
  if (key === 'ArrowDown') navigateToNextChild();
  if (key === 'ArrowUp') navigateToPrevChild();
  if (key === 'Escape' && typeof escapeCb === 'function') escapeCb();
};