import React from 'react';
import PropTypes from 'prop-types';
import styled, { css } from 'styled-components';

const Style = css`
  display: flex;

  h2 {
    display: none;
  }

  ul {
    list-style: none;
    display: flex;
    padding: 0;
    margin: 0;
    align-items: center;
    width: 100%;
  }
`;

const NavList = styled(({ children, labelledby, titleLabelledby, ...rest }) => (
  <nav aria-labelledby={labelledby} {...rest}>
    <h2 id={labelledby}>{titleLabelledby}</h2>

    <ul>{children}</ul>
  </nav>
))`
  ${Style}
`;

NavList.defaultProps = {
  children: null,
};

NavList.propTypes = {
  children: PropTypes.oneOfType([PropTypes.element, PropTypes.string, PropTypes.array, PropTypes.node]),
  labelledby: PropTypes.string.isRequired,
  titleLabelledby: PropTypes.string.isRequired,
};

export default NavList;
