import { useEffect, useState } from 'react';

const userContextRoute = process.env.NODE_ENV === 'development' ? 'http://localhost:9999' : '';

const AllowForPartner = ({ children }) => {
  const [partnership, setPartnership] = useState(false);

  useEffect(() => {
    fetch(`${userContextRoute}/api/user_context`)
      .then(response => response.json())
      .then(
        ({
          context: {
            user: { partnership },
          },
        }) => {
          setPartnership(partnership);
        },
      )
      .catch(error => setPartnership(false));
  }, []);

  return partnership ? children : null;
};

export default AllowForPartner;
