import { initReactI18next, I18nextProvider } from 'react-i18next'
import React from 'react'
import i18nFactory from '../i18n-api/i18n-factory'

export default (Component, configuration) => {
  const i18n = i18nFactory(configuration)(initReactI18next)
  return props => (
    <I18nextProvider i18n={i18n}>
      <Component {...props} />
    </I18nextProvider>
  )
}
