import React from 'react';
import PropTypes from 'prop-types';
import styled, { css } from 'styled-components';
import { useAuthorization } from '@resultadosdigitais/front-hub/react';
import { CaretDown } from '@resultadosdigitais/tangram-react-icons';
import { useTranslation } from 'react-i18next';
import { Button, Text, DropdownMenu, Tag } from '@resultadosdigitais/tangram-components';

import { isPathActive } from '../../helpers';
import schema from './schema';

import DropdownMenuList from '../../components/DropdownMenuList';
import DropdownMenuItem from '../../components/DropdownMenuItem';

const StyledDropdownMenu = styled(DropdownMenu)`
  width: 216px;
  border-left: 1px solid var(--color-gray-20);

  .account-menu__caret {
    fill: var(--color-gray-60);
    margin-left: 0;
  }
`;

const ButtonHoverActived = css`
  outline: none;

  .account-menu__name {
    color: var(--color-gray-90);
  }

  .account-menu__account {
    color: var(--color-gray-70);
  }

  .account-menu__caret {
    fill: var(--color-gray-70);
  }
`;

const StyledButton = styled(Button)`
  outline: none;
  border-radius: 0;
  padding: 14px var(--size-spacing-04);
  transition-duration: var(--motion-duration-extra-fast);
  text-align: left;
  align-items: center;
  margin: 0;
  height: 100%;

  &&[aria-expanded='true'] {
    background-color: var(--color-gray-10);
    ${ButtonHoverActived}
    margin: 0;
  }

  &:hover {
    background-color: transparent;
    ${ButtonHoverActived}
  }
`;

const StyledText = styled(Text)`
  display: block;
  overflow: hidden;
  text-overflow: ellipsis;
  width: 136px;
  white-space: nowrap;
`;

const StyledInfos = styled.div`
  margin: 0 var(--size-spacing-03);
`;

const StyledDropdownMenuList = styled(DropdownMenuList)`
  width: 270px;
`;

const StyledDropdownMenuItem = styled(DropdownMenuItem)`
  ${({ selected }) =>
    selected &&
    css`
      color: var(--color-primary-60);

      &:hover,
      &:active {
        color: var(--color-primary-70);
      }
    `}
`;

const StyledMenuItemRelative = styled.div`
  position: relative;
`;

const StyledTag = styled(Tag)`
  position: absolute;
  z-index: 9;
  top: 10px;
  right: 13px;
  box-sizing: content-box;
`;

const AccountMenu = ({ userFirstName, userLastName, userEmail, accountName, accountId, ...rest }) => {
  const { t: translate, i18n } = useTranslation();
  const Allowed = useAuthorization();

  const { accountItem, integrationItem, successPlansItem, myJourneyItem, myAccount } = schema;

  const userName = () => {
    const name = `${userFirstName ? userFirstName : ''} ${userLastName ? userLastName : ''}`;

    if (name.trim() !== '') return name;

    return userEmail;
  };

  return (
    <StyledDropdownMenu {...rest}>
      <DropdownMenu.Trigger id="account-menu">
        <StyledButton kind={Button.kind.TERTIARY} endIcon={<CaretDown className="account-menu__caret" />}>
          <StyledInfos>
            <StyledText
              forwardedAs="span"
              token={Text.tokens.FONT_SIZE_SM_SEMIBOLD}
              textColor="--color-gray-70"
              className="account-menu__name"
              dangerouslySetInnerHTML={{ __html: userName() }}
            />
            <StyledText forwardedAs="span" token={Text.tokens.FONT_SIZE_XS_SEMIBOLD} textColor="--color-gray-60" className="account-menu__account">
              {accountName}
            </StyledText>
          </StyledInfos>
        </StyledButton>
      </DropdownMenu.Trigger>

      <StyledDropdownMenuList position={DropdownMenu.position.RIGHT}>
        <DropdownMenu.Title>{translate('account.profileTitle')}</DropdownMenu.Title>

        <StyledMenuItemRelative>
          <StyledDropdownMenuItem selected={isPathActive(accountItem)} href="/configuracoes">
            {translate('account.accountItem')}
          </StyledDropdownMenuItem>
          {accountId && <StyledTag>ID: {accountId}</StyledTag>}
        </StyledMenuItemRelative>

        <DropdownMenu.Divider />
        <StyledDropdownMenuItem selected={isPathActive(integrationItem)} href="/integracoes">
          {translate('account.integrationItem')}
        </StyledDropdownMenuItem>
        <Allowed permission="accountMenu:successPlans">
          <StyledDropdownMenuItem selected={isPathActive(successPlansItem)} href="/planos-de-sucesso">
            {translate('account.successPlansItem')}
          </StyledDropdownMenuItem>
        </Allowed>
        <Allowed permission="accountMenu:myJourney">
          <StyledDropdownMenuItem selected={isPathActive(myJourneyItem)} href="/minha-jornada">
            {translate('account.myJourneyItem')}
          </StyledDropdownMenuItem>
        </Allowed>

        <StyledDropdownMenuItem selected={isPathActive(myAccount)} href="/configuracoes/minhas-contas">
          {translate('account.myAccount')}
        </StyledDropdownMenuItem>
        <DropdownMenu.Divider />
        <DropdownMenuItem href={`/logout?locale=${i18n.language}`}>{translate('account.exitItem')}</DropdownMenuItem>
      </StyledDropdownMenuList>
    </StyledDropdownMenu>
  );
};

AccountMenu.defaultProps = {
  userName: null,
  userLastName: null,
  userEmail: null,
  accountName: null,
  accountId: null,
};

AccountMenu.propTypes = {
  userName: PropTypes.string,
  userLastName: PropTypes.string,
  userEmail: PropTypes.string,
  accountName: PropTypes.string,
  accountId: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
};

export default AccountMenu;
