import { pushOrSplice } from './functions'
import { TOKEN_TYPE } from './tokens'
import tokenizer from './tokenizer'
import compiler from './compiler'

export default (tokens, permissions, context) => {
  let remove = false

  const grammar = {
    [TOKEN_TYPE.NOT]: () => {
      remove = true
    },
    [TOKEN_TYPE.RESOURCE]: () => {},
    [TOKEN_TYPE.EXTENDS]: idx => {
      if (!tokens[idx + 1] || tokens[idx + 1].type !== TOKEN_TYPE.ROLE) {
        throw new Error('front-hub: rbac you can only extends roles')
      }
    },
    [TOKEN_TYPE.PERMISSION]: idx => {
      const token = tokens[idx]
      const permission = tokens[idx - 1].value + ':' + token.value
      pushOrSplice(remove, permissions, permission)
    },
    [TOKEN_TYPE.PERMIT_ALL]: idx => {
      const resourceName = tokens[idx - 1].value
      const resource = context.permissions[resourceName]
      if (!Array.isArray(resource)) {
        throw new Error('front-hub: rbac resources ' + resourceName + ' was not found or must be an array')
      }

      resource
        .map(perm => tokens[idx - 1].value + ':' + perm)
        .forEach(perm => {
          pushOrSplice(remove, permissions, perm)
        })
    },
    [TOKEN_TYPE.ROLE]: idx => {
      const token = tokens[idx]
      const grantName = token.value
      const grant = context.grants[grantName]
      if (!Array.isArray(grant)) {
        throw new Error('front-hub: rbac grants ' + grantName + ' must be an array')
      }
      grant.forEach(grant => {
        const role_tokens = tokenizer(grant)
        compiler(role_tokens, permissions, context)
      })
    },
  }

  tokens.forEach((lexical, idx) => {
    if (!grammar[lexical.type]) throw new Error('front-hub: rbac undefied token ' + lexical.type)
    grammar[lexical.type](idx)
  })

  return permissions
}
