/** @format */

export default function useURLManager(history, debug = false) {
  if (!history || typeof history.listen !== 'function') throw new Error('front-hub: URLManager must be initialized with a valid history instance')
  const dispatchers = [];
  function broadcast() {
    const previous = getState();
    dispatchers.forEach(dispatch => dispatch(previous));
  }

  // TODO: do a better job here
  function setState(append) {
    var searchParams = new URLSearchParams(history.location.search);
    const current = JSON.parse(atob(searchParams.get('f') || 'e30='));
    current[append.name] = append;
    searchParams.set('f', btoa(JSON.stringify(current)));
    history.push({ search: searchParams.toString() });
  }

  function getState() {
    var searchParams = new URLSearchParams(history.location.search);
    const current = JSON.parse(atob(searchParams.get('f') || 'e30='));
    return current;
  }

  function register(privateHistory, name) {
    const dispacher = ((closureHistory, closureName) => currentState => {
      if (currentState[closureName]) {
        closureHistory.push({
          rehydratation: true,
          ...currentState[closureName]
        });
      }
    })(privateHistory, name);

    dispacher(getState());
    dispatchers.push(dispacher);

    return () => {
      const index = dispatchers.indexOf(dispacher);
      /* istanbul ignore else  */
      if (index > -1) {
        dispatchers.splice(index, 1);
      }
    };
  }

  let disableGlobalListen = history.listen(broadcast)
  function dispatch(params) {
    disableGlobalListen();
    setState(params)
    disableGlobalListen = history.listen(broadcast)
  }

  return function listen(privateHistory, name) {
    const fired = {};
    const unListen = privateHistory.listen(params => {
      if (fired[params.key] || params.rehydratation) return;

      fired[params.key] = true;
      dispatch({
        ...params,
        name
      })

      /* istanbul ignore if  */
      if (debug) {
        console.info(' front-hub: ' + name + ' fired ' + JSON.stringify(params));
      }

    })

    const unRegister = register(privateHistory, name);

    /* istanbul ignore if  */
    if (debug) {
      console.info('front-hub: ' + name + ' registred.');
    }
    return () => {
      unListen();
      unRegister();
      /* istanbul ignore if  */
      if (debug) {
        console.info('front-hub: ' + name + ' removed.');
      }
    };
  }
}
