/* globals MICROFRONTEND_NAME */
/* eslint-disable global-require */
import React from 'react'
import ReactDOM from 'react-dom'
import { MemoryRouter } from 'react-router-dom'

import rootCompomentFactory from '../../../common/microfrontend/react/components/root-component-factory'
import instancePublicApi from '../../../common/microfrontend/react/context-hub-api'

export default function microfrontendFactory(Component, fronthubContext) {
  const configuration = fronthubContext.configuration || fronthubContext;
  const instance = {
    listeners: {},
    delegates: {},
    deferedDelegates: {},
    configuration,
    manifestInstance: fronthubContext.manifestInstance
  }
  const RootComponent = rootCompomentFactory(instance, Component, MemoryRouter)
  const publicApi = instancePublicApi(instance)
  return {
    ...publicApi,
    renderAt(DOMElement, params = {}) {
      const actualDOMElement = typeof DOMElement === 'string' ? document.querySelector(DOMElement) : DOMElement
      if (actualDOMElement) {
        actualDOMElement.setAttribute('data-front-hub', MICROFRONTEND_NAME)
        actualDOMElement['front-hub'] = { configuration }
        ReactDOM.render(<RootComponent params={params} />, actualDOMElement)
      } else {
        throw new Error(
          `front-hub: ops... there is something wrong with the DOMElement you passed to render your application: \n "${actualDOMElement}"\n\n`,
        )
      }
    },
  }
}
