/* eslint-disable import/no-unresolved */
/* eslint-disable max-classes-per-file */
// eslint-disable-next-line import/no-unresolved
/* globals DISABLE_LOCALIZATION */
import React from 'react'

import contextHubApi from '../context-hub-api'
import { FronthubProvider } from 'front-hub-context-api'
import WatchURLChanges from './watch-url-changes'
import { parseToSafeJson } from '../../../../core/security'

export default function rootCompomentBuilder(fronthubContext, Component, Router) {
  const contextPublicApi = contextHubApi(fronthubContext)

  if (!DISABLE_LOCALIZATION) {
    // eslint-disable-next-line global-require
    Component = require('../i18n-hoc').default(Component, fronthubContext.configuration) // tree shaking
  }

  console.log('DISABLE_LOCALIZATION', DISABLE_LOCALIZATION)

  return ({ params }) => (
    <Router initialEntries={['/']} initialIndex={0}>
      <WatchURLChanges microfrontend={fronthubContext.manifestInstance} />
      <FronthubProvider value={contextPublicApi}>
        <Component {...parseToSafeJson(params)}></Component>
      </FronthubProvider>
    </Router>
  )
}
